.arrow:hover {
  cursor: pointer;
}
.arrow-left {
  margin-right: 6px;
}
.arrow-left::before {
  position: absolute;
  right: calc(50% - 4.5px);
  top: calc(50% - 3px);
  content: '';
  display: block;
  height: 5px;
  width: 5px;
  border-bottom: 2px solid #0501ef;
  border-left: 2px solid #0501ef;
  transform: rotate(45deg);
}
.arrow-right::before {
  position: absolute;
  right: calc(50% - 3px);
  top: calc(50% - 3px);
  content: '';
  display: block;
  height: 5px;
